/* Toolbar
--------------------------------------------------------------------------------------------------*/
.fc-toolbar { text-align: center; margin-bottom: 1em; }

.fc-toolbar .fc-left { float: left; }

.fc-toolbar .fc-right { float: right; }

.fc-toolbar .fc-center { display: inline-block; }

